
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
name: 'DialogInput',
props: {
isShow: {
type: Boolean,
default: false,
required: true
},
inputItems: {
type: Array,
default: [],
required: false
}
},
components: {
XButton, XInput, Group
},
data () {
return {
values: [],
names: {_from: ':', _to: ':', _value: ':', owner_: ':', _spender: ':', _subtractedValue: ':', _owner: ':', mintedAmount: ':', _addedValue: ':', target: ':', freeze: ':', address: ':', uint256: ':',bool:'1:0:', psd: ':', password: ':', _useAddress: ':', _contractAddress: ':', _defaultCoin: ':', _months: '():', _mdays: '():', uint8: '', _currentYearGet: ':'
}
}
},
methods: {
closeMyself() {
this.$emit("on-close")
},
tradeBtn () {
var that = this
let values = that.values
that.$emit("trades-btn",{values:values})
that.values = []
}
}





}



